<template>
  <div class="loginBlock">
    <div class="loginBlock__block">
      <p class="loginBlock__header">使用FaceBook登入</p>
      <div class="loginBlock__body">
        <div class="testB">FACEBOOK</div>
      </div>
    </div>
    <div class="loginBlock__block"
    v-if="for_login">
      <p class="loginBlock__header">使用帳號登入</p>
      <div class="loginBlock__body">
        <form class="loginBlock__form" @submit.prevent="submit_form">
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              E-mail
            </label>
            <input type="email"
            v-model="login_form.email"
            class="loginBlock__form__input" required>
            <label class="loginBlock__form__checkbox">
              <input type="checkbox" v-model="login_form.remember_account">
              記住帳號
            </label>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              Password
            </label>
            <input type="password"
             v-model="login_form.password"
            class="loginBlock__form__input" required>
            <a href="#" class="loginBlock__form__a">忘記密碼?</a>
          </div>
          <div class="loginBlock__form__group">
            <captcha></captcha>
          </div>
          <div class="loginBlock__form__result">
            <button type="submit"
            class="loginBlock__form__btn loginBlock__form__btn--primary">
              登入
            </button>
            <button
            class="loginBlock__form__btn loginBlock__form__btn--secondary"
            @click.prevent="for_login = !for_login;
                            for_register = !for_register">
              還不是會員，加入會員
            </button>
          </div>
        </form>
      </div>
    </div>
    <div class="loginBlock__block"
    v-if="for_register">
      <p class="loginBlock__header">註冊帳號</p>
      <div class="loginBlock__body">
        <form class="loginBlock__form" @submit.prevent="submit_form">
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              姓名
            </label>
            <input type="text"
            v-model="register_form.name"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              性別
            </label>
            <div class="loginBlock__form__radio"
            v-for="(item, index) in gender_options"
            :key="index">
              <label>
                <input type="radio"
                :value="item.english"
                v-model="register_form.gender"
                class="loginBlock__form__input" required>
                {{item.chinese}}
              </label>
            </div>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              生日
            </label>
            <input type="date"
            v-model="register_form.birthday"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              電話
            </label>
            <input type="text"
            v-model="register_form.tel"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              E-mail
            </label>
            <input type="email"
            v-model="register_form.email"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              設定密碼
            </label>
            <input type="password"
            v-model="register_form.password"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__group">
            <label class="loginBlock__form__title">
              確認密碼
            </label>
            <input type="password"
            @input="confirm_password($event.target.value)"
            class="loginBlock__form__input" required>
          </div>
          <div class="loginBlock__form__result">
            <button type="submit"
            class="loginBlock__form__btn loginBlock__form__btn--primary">
              註冊
            </button>
            <button
            class="loginBlock__form__btn loginBlock__form__btn--secondary"
            @click.prevent="for_login = !for_login;
                for_register = !for_register">
              已是會員，會員登入
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Captcha from './Captcha.vue';

export default {
  components: { Captcha },
  data() {
    return {
      for_login: true,
      for_register: false,
      login_form: {
        email: '',
        remember_account: false,
        password: '',
      },
      register_form: {
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
      },
      gender_options: [
        {
          chinese: '男',
          english: 'male',
        },
        {
          chinese: '女',
          english: 'female',
        },
      ],
      is_captcha_same: false,
      is_password_same: false,
    };
  },
  methods: {
    submit_form() {
      let correct = 0;
      const confirmation = 2;

      if (this.register_form.gender !== '') {
        correct += 1;
      } else {
        alert('請填寫性別');
      }

      if (this.is_password_same) {
        correct += 1;
      } else {
        alert('密碼確認錯誤');
      }

      if (correct === confirmation) {
        console.log('sumbit');
      }
    },
    confirm_password(password) {
      if (password === this.register_form.password) {
        this.is_password_same = true;
      } else {
        this.is_password_same = false;
      }
    },
  },
  mounted() {
    this.$bus.$on('emit_captcha', (boolean) => {
      this.is_captcha_same = boolean;
    });
  },
};
</script>
